.show-hide-button {
    display: block;
    position: relative;
    color: white;
    background: transparent !important;
    padding: 10px;
    border: 1px solid #dc232e !important;
    text-transform: uppercase;
    padding-left: 15px;
    padding-right: 15px;
    font-size: 14px;
    /* font-weight: bold; */
    overflow: hidden;
    transition: color 0.45s cubic-bezier(0.785, 0.135, 0.15, 0.86), border 0.45s cubic-bezier(0.785, 0.135, 0.15, 0.86);
    z-index: 1;
}

.show-hide-button > .review-show-button {
    z-index: 5;
    position: relative;
    transition: color 350ms ease;
}

.show-hide-button:hover .review-show-button {
    color: black !important;
}

.show-hide-button::after, .show-hide-button::before {
    content: " ";
    display: block;
    position: absolute;
    width: 100%;
    top: 0;
    bottom: 0;
    right: 0;
    background: #dc232e !important;
